import { ref } from 'vue';
import { AccountStatus, Routes } from '@/utils/enums';
import { genPassword, getNameInitials } from '@/utils/functions';
import AccessManager from '@/mixins/access-manager';
import MailingMixin from '@/mixins/mailing';
import NotificationMixin from '@/mixins/notification';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import WebsiteInfoMixin from '@/mixins/website-info';
import { USER_CONTEXT } from '@/store/modules/database/user';
import sanitizer from 'string-sanitizer';
import useClipboard from 'vue-clipboard3';
import { mapActions } from 'vuex';
export default {
  name: 'Users',
  mixins: [ViewsBaseMixin, AccessManager, MailingMixin, SettingsMixin, WebsiteInfoMixin, NotificationMixin],
  data() {
    return {
      Routes,
      user: {
        name: '',
        email: '',
        access: '',
        status: false
      },
      rules: {
        name: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        access: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      currentEditItem: null,
      sendLoginInfo: false,
      userDialog: false,
      isLoading: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.name.toLowerCase().includes(this.search.toLowerCase()));
    },
    isEmailValid() {
      const valid = this.$validator.isEmail(this.user.email);
      if (!valid) this.sendLoginInfo = false;
      return valid;
    }
  },
  created() {
    this.init();
  },
  methods: {
    getNameInitials,
    ...mapActions({
      getUserByCredentials: 'authentication/getUserByCredentials'
    }),
    ...mapActions('user', ['getAllUsers', 'checkUser']),
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    async init() {
      this.isLoading = true;
      await this.getAllAccess();
      const resp = await this.getAllUsers();
      if (resp.success) {
        if (resp.data) this.tableData = resp.data;
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
      this.isLoading = false;
    },
    async genUid(uidSrc, addSalt) {
      var uid = sanitizer.sanitize(uidSrc.toLowerCase()).substring(0, 10);
      if (addSalt) uid += this.$_.random(1, 99, false);
      var task = await this.checkUser(uid);
      if (task.success && !task.data) {
        return uid;
      } else {
        return await this.genUid(uidSrc, true);
      }
    },
    addItem() {
      this.currentEditItem = null;
      this.user.name = '';
      this.user.email = '';
      this.user.access = '';
      this.user.status = false;
      this.userDialog = true;
    },
    async editItem(index, item) {
      this.currentEditItem = item;
      this.user.name = item.name;
      this.user.email = item.email;
      this.user.access = item.access;
      this.user.status = !!parseInt(item.status);
      this.userDialog = true;
    },
    deleteItem(index, item) {
      this.$prompt(this.$t('deleteUserQuestion'), this.$t('delete'), {
        inputPlaceholder: this.$t('confirmWithPassword'),
        confirmButtonText: this.$t('confirm'),
        cancelButtonText: this.$t('cancel'),
        inputType: 'password'
      }).then(async ({
        value
      }) => {
        if (value) {
          const resp = await this.getUserByCredentials({
            username: this.userData.uid,
            password: value
          });
          if (resp.success) {
            if (resp.data.username && resp.data.password && resp.data.user) {
              const task = await this.deleteItemInDB({
                context: USER_CONTEXT,
                id: item.id
              });
              if (task.success) {
                this.$message.success(this.$t('success'));
                this.init();
              } else {
                this.$message.error(this.$t('anErrorOccured'));
              }
            } else {
              this.$message.error(this.$t('wrongPassword'));
            }
          } else {
            this.$message.error(this.$t('anErrorOccured'));
          }
        }
      }).catch(() => {
        /* empty */
      });
    },
    submitForm(ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          if (this.user.email && !this.isEmailValid) {
            this.$message.warning(this.$t('invalidEmail'));
            return false;
          }
          let credentials = {};
          if (ref === 'addForm') {
            const uid = await this.genUid(this.user.name, false);
            credentials = {
              uid: uid,
              name: this.user.name,
              email: this.user.email,
              access: this.user.access,
              status: this.user.status ? AccountStatus.ENABLED : AccountStatus.DISABLED,
              hash_id: this.$uuidv4(),
              password: genPassword()
            };
            const task = await this.addItemInDB({
              context: USER_CONTEXT,
              data: credentials
            });
            if (task.success) {
              this.addNotification({
                type: this.NotificationTypes.WELCOME,
                recipients: [task.data],
                params: {},
                source: this.NotificationSources.SYSTEM,
                target: '',
                removable: true
              });
              this.$message.success(this.$t('success'));
            } else this.$message.error(this.$t('anErrorOccured'));
          } else {
            const newData = {};
            credentials = this.currentEditItem;
            if (this.currentEditItem.name !== this.user.name) {
              newData['name'] = this.user.name;
            }
            if (this.currentEditItem.email !== this.user.email) {
              newData['email'] = this.user.email;
            }
            if (this.currentEditItem.access !== this.user.access) {
              newData['access'] = this.user.access;
            }
            if (!!parseInt(this.currentEditItem.status) !== this.user.status) {
              newData['status'] = this.user.status ? AccountStatus.ENABLED : AccountStatus.DISABLED;
            }
            if (!this.$_.isEmpty(newData)) {
              const task = await this.updateItemInDB({
                context: USER_CONTEXT,
                id: this.currentEditItem.id,
                data: newData
              });
              if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
            }
          }
          if (this.sendLoginInfo) {
            this.sendEmail(null, `${this.$t('loginInfo')} - ${this.tr(this.websiteInfo.title)} - ${process.env.VUE_APP_NAME}`, `${this.$t('loginInfo')} - ${this.tr(this.websiteInfo.title)} - ${process.env.VUE_APP_NAME}`, `<small><b>${this.$t('id')}</b> : ${credentials.uid} <br> <b>${this.$t('password')}</b> : ${credentials.password}</small>`, credentials.email);
          }
          this.cancelForm(ref);
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm(ref) {
      this.$refs[ref].resetFields();
      this.userDialog = false;
    },
    viewPassword(password) {
      this.$alert(password, {
        center: true,
        showClose: true,
        closeOnClickModal: true,
        showCancelButton: false,
        showConfirmButton: false,
        closeOnPressEscape: true,
        icon: 'lock'
      });
    },
    async copyPassword(password) {
      const {
        toClipboard
      } = useClipboard();
      try {
        await toClipboard(password);
        this.$message.success(this.$t('success'));
      } catch (e) {
        console.error(e);
        this.$message.error(this.$t('anErrorOccured'));
      }
    }
  }
};