import { ref } from 'vue';
import "core-js/modules/es.array.push.js";
import { AppScopes, Routes } from '@/utils/enums';
import AccessManager from '@/mixins/access-manager';
import ViewsBaseMixin from '@/mixins/views-base';
import { ACCESS_CONTEXT } from '@/store/modules/database/access';
import { mapActions, mapGetters } from 'vuex';
export default {
  name: 'AccessManagement',
  mixins: [ViewsBaseMixin, AccessManager],
  data() {
    return {
      Routes,
      users: [],
      appScopes: [],
      access: {
        name: '',
        scopes: []
      },
      rules: {
        name: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        scopes: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      currentEditItem: null,
      accessDialog: false,
      isLoading: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    ...mapGetters({
      disabledModuleScopes: 'access/getDisabledModuleScopes'
    }),
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.name.toLowerCase().includes(this.search.toLowerCase()));
    }
  },
  created() {
    this.init();
    this.$_.forIn(AppScopes, (value, key) => {
      if (value.display && this.disabledModuleScopes.findIndex(s => s.value == value.value) === -1) this.appScopes.push(value);
    });
  },
  methods: {
    ...mapActions({
      getAllUsers: 'user/getAllUsers',
      getAllAccess: 'access/getAllAccess',
      getUserByCredentials: 'authentication/getUserByCredentials'
    }),
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    showAccessScopes(scopes) {
      if (scopes) {
        let result = '';
        const tab = scopes.split(';');
        for (let i = 0; i < tab.length; i++) {
          const scope = this.appScopes.find(a => a.value == tab[i]);
          if (scope) {
            result += scope.label;
            if (i + 1 < tab.length) result += ' / ';
          }
        }
        return result;
      }
      return '';
    },
    showAccessUsers(id) {
      if (id) {
        let result = '';
        const users = this.users.filter(u => u.access == id);
        for (let i = 0; i < users.length; i++) {
          result += users[i].name;
          if (i + 1 < users.length) result += ' / ';
        }
        return result;
      }
      return '';
    },
    getAppScopeType(item) {
      if (item.section) return this.$t('section');
      if (item.action) return this.$t('action');
      if (item.module) return this.$t('module');
      if (item.page) return this.$t('pages');
    },
    async init() {
      this.isLoading = true;
      const resp0 = await this.getAllUsers();
      if (resp0.data) this.users = resp0.data;
      const resp = await this.getAllAccess();
      if (resp.data) this.tableData = resp.data;
      this.isLoading = false;
    },
    addItem() {
      this.currentEditItem = null;
      this.access.name = '';
      this.access.scopes = [];
      this.accessDialog = true;
    },
    async editItem(index, item) {
      this.currentEditItem = item;
      this.access.name = item.name;
      this.access.scopes = item.scopes ? item.scopes.split(';') : [];
      this.accessDialog = true;
    },
    deleteItem(index, item) {
      this.$prompt(this.$t('deleteAccessWarning'), this.$t('deleteAccess'), {
        inputPlaceholder: this.$t('confirmWithPassword'),
        confirmButtonText: this.$t('confirm'),
        cancelButtonText: this.$t('cancel'),
        inputType: 'password'
      }).then(async ({
        value
      }) => {
        if (value) {
          const resp = await this.getUserByCredentials({
            username: this.userData.uid,
            password: value
          });
          if (resp.success) {
            if (resp.data.username && resp.data.password && resp.data.user) {
              const task = await this.deleteItemInDB({
                context: ACCESS_CONTEXT,
                id: item.id
              });
              if (task.success) {
                this.$message.success(this.$t('success'));
                this.init();
              } else {
                this.$message.error(this.$t('anErrorOccured'));
              }
            } else {
              this.$message.error(this.$t('wrongPassword'));
            }
          } else {
            this.$message.error(this.$t('anErrorOccured'));
          }
        }
      }).catch(() => {
        /* empty */
      });
    },
    submitForm(ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          if (ref === 'addForm') {
            if (this.tableData.findIndex(d => d.name == this.access.name) > -1) {
              this.$message.error(this.$t('sameNameErrorMessage'));
              return false;
            }
            const task = await this.addItemInDB({
              context: ACCESS_CONTEXT,
              data: {
                name: this.access.name,
                scopes: this.access.scopes.join(';')
              }
            });
            if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
          } else {
            const newData = {};
            if (this.currentEditItem.name !== this.access.name) {
              newData['name'] = this.access.name;
            }
            if (this.currentEditItem.scopes !== this.access.scopes) {
              newData['scopes'] = this.access.scopes.join(';');
            }
            if (!this.$_.isEmpty(newData)) {
              const task = await this.updateItemInDB({
                context: ACCESS_CONTEXT,
                id: this.currentEditItem.id,
                data: newData
              });
              if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
            }
          }
          this.cancelForm(ref);
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm(ref) {
      this.$refs[ref].resetFields();
      this.accessDialog = false;
    }
  }
};